<template>
    <v-container id="quantitative">
        <v-row>
            <h3>Quantitative Analysis</h3>
        </v-row>
        <v-container>
            <v-row class="subsection">
                <h3>Income Statements</h3>
                <v-row>
                    <v-col cols="10" sm="3">
                        <h4>Operating Profit Margin: <u>{{operatingProfitMargin}}</u>%</h4>
                    </v-col>
                   <v-tooltip top>
                       <template v-slot:activator="{ on, attrs }">
                            <v-col cols="2" sm="1" class="pl-0" v-bind="attrs" v-on="on">
                                <span v-if="operatingProfitMargin >= 8"><v-btn x-small color="green">Pass</v-btn></span><span v-else><v-btn x-small color="error">Fail</v-btn></span>
                            </v-col>
                       </template>
                       <span>OPM Above 8%</span>
                   </v-tooltip>

                    <v-col cols="10" sm="3"><h4>Operating income trend</h4>
                        {{ reportedYear - 2 }}: {{   operatingIncomeLastLast   }}<br>
                         {{ reportedYear - 1 }}: {{  operatingIncomeLast  }} <br>
                    {{ reportedYear }}: {{  operatingIncome }} <br>
                        <h4>CAGR: <u>{{cagr}}</u> %</h4>
                    </v-col>
                   <v-tooltip bottom>
                       <template v-slot:activator="{ on, attrs }">
                            <v-col cols="2" sm="1" class="pl-0" v-bind="attrs" v-on="on">
                                <span v-if="cagr >= 10"><v-btn x-small color="green">Pass</v-btn></span><span v-else><v-btn x-small color="error">Fail</v-btn></span>
                            </v-col>
                       </template>
                       <span>CAGR Above 10%</span>
                   </v-tooltip>

                    <v-col cols="10" sm="3">
                        <canvas id="myChart"></canvas>
                    </v-col>
                </v-row>
            </v-row>
            <v-row class="subsection">
                <h3>Balance Sheet</h3>
                <v-row>
                    <v-col cols="10"><h4>Net Debt to Equity</h4>
                        Total Debt : {{  totalDebt  }}<br>
                        Total Cash : {{  totalCash }}<br>
                        Total Equity: {{  shareholderEquityDisplay  }}<br>
                        <h4>Net Debt to Equity : <u>{{  netDebtToEquity  }}</u>%</h4>
                    </v-col>
                   <v-tooltip bottom>
                       <template v-slot:activator="{ on, attrs }">
                            <v-col cols="2" class="pl-0" v-bind="attrs" v-on="on">
                                <span v-if="netDebtToEquity <= 40 && shareholderEquity > 0"><v-btn x-small color="green">Pass</v-btn></span><span v-else><v-btn x-small color="error">Fail</v-btn></span>
                            </v-col>
                       </template>
                       <span>Net Debt to Equity Less Than 40%</span>
                   </v-tooltip>
                </v-row>
                <v-row>
                    <v-col cols="10">
                        <h4>Return on Equity:  <u>{{returnOnEquity}}</u>%</h4>
                    </v-col>
                   <v-tooltip bottom>
                       <template v-slot:activator="{ on, attrs }">
                            <v-col cols="2" class="pl-0" v-bind="attrs" v-on="on">
                                <span v-if="returnOnEquity >= 15"><v-btn x-small color="green">Pass</v-btn></span><span v-else><v-btn x-small color="error">Fail</v-btn></span>
                            </v-col>
                       </template>
                       <span>Return on Equity Above 15%</span>
                   </v-tooltip>
                </v-row>
            </v-row>
            <v-row class="subsection">
                <h3>Cash Flow Statement</h3>
                <v-row no-gutters>
                    <v-col cols="10" sm="3"><h4>Life Ratio</h4>
                        Operating Cashflow : {{  cashFlow  }}<br>
                        Net Income : {{  netIncome  }}
                        <h4>Life Ratio: <u>{{  lifeRatio  }}</u></h4>
                    </v-col>
                   <v-tooltip bottom>
                       <template v-slot:activator="{ on, attrs }">
                           <v-col cols="2" sm="1" class="pl-0" v-bind="attrs" v-on="on">
                                <span v-if="lifeRatio >= 0.8 && cashFlowRaw > 0"><v-btn x-small color="green">Pass</v-btn></span><span v-else><v-btn x-small color="error">Fail</v-btn></span>
                            </v-col>
                       </template>
                       <span>Life Ratio Above 0.8</span>
                   </v-tooltip>

                    <v-col cols="10" sm="3"><h4>Vampire ratio</h4>
                        Capital Expenditure : {{ PaymentsToAcquirePropertyPlantAndEquipment }}<br>
                        Operating Cashflow : {{ cashFlow }}<br>
                        <h4>CAPEX / CFO : <u>{{vampireRatio}}</u></h4>
                    </v-col>
                   <v-tooltip bottom>
                       <template v-slot:activator="{ on, attrs }">
                            <v-col cols="2" sm="1" class="pl-0" v-bind="attrs" v-on="on">
                                <span v-if="vampireRatio <= 0.5 && cashFlowRaw > 0"><v-btn x-small color="green">Pass</v-btn></span><span v-else><v-btn x-small color="error">Fail</v-btn></span>
                            </v-col>
                       </template>
                       <span>CAPEX / CFO Below 0.5</span>
                   </v-tooltip>

                    <v-col cols="10" sm="3"><h4>Cash Conversion Cycle</h4>
                        Receivable Days : {{daysSalesOutstanding}}<br>
                        Inventory Days: {{daysInventory}}<br>
                        Payable Days: {{daysPayablesPeriod}}<br>
                        <h4>Cash Conversion Cycle Days <u>{{ cashConversionCycleDays }}</u></h4>
                    </v-col>
                   <v-tooltip bottom>
                       <template v-slot:activator="{ on, attrs }">
                            <v-col cols="2" sm="1" class="pl-0" v-bind="attrs" v-on="on">
                                <span v-if="vampireRatio <= 130"><v-btn x-small color="green">Pass</v-btn></span><span v-else><v-btn x-small color="error">Fail</v-btn></span>
                            </v-col>
                       </template>
                       <span>Cash Conversion Cycle Days Below 130 Days</span>
                   </v-tooltip>
                </v-row>
            </v-row>
        </v-container>
    </v-container>
</template>

<script>
    var numeral = require('numeral');
    import Chart from 'chart.js';

    // For the charts postitive and negative numbers
    const posColour = 'rgba(20,128,41,0.5)';
    const negColour = 'rgba(220,20,60,0.5)';

    // To fix the bug where a fill integer is too large in chart.js??
    function limitToZeroOne(float){
                if (float > 1.0 ){
                    float = 1.0;
                }
                else if (float < 0.0){
                    float = 0.0
                }
                return float
            }

    export default {
        name: "Quantitative.vue",
        computed: {
                operatingProfitMargin() {
                    return this.$store.getters.operatingProfitMargin();
                },
                reportedYear() {
                    return this.$store.getters.reportedYear();
                },
                operatingIncome() {
                    let n = this.$store.getters.operatingIncome();
                    n = numeral(n).format('0.00 a');
                    return n;
                },
                operatingIncomeLast() {
                    let n = this.$store.getters.operatingIncomeLast();
                    n = numeral(n).format('0.00 a');
                    return n;
                },
                operatingIncomeLastLast() {
                    let n = this.$store.getters.operatingIncomeLastLast();
                    n = numeral(n).format('0.00 a');
                    return n;
                },
                ebitTrend(){
                    return [this.$store.getters.operatingIncomeLastLast(), this.$store.getters.operatingIncomeLast(), this.$store.getters.operatingIncome()]
                },
                cagr() {
                    return this.$store.getters.cagr();
                },
                totalDebt(){
                    let n = this.$store.getters.totalDebt();
                    n = numeral(n).format('0.00 a');
                    return n;
                },
                totalCash(){
                    let n = this.$store.state.financials[0]['totalCash'];
                    n = numeral(n).format('0.00 a');
                    return n;
                },
                shareholderEquity(){
                    return this.$store.state.financials[0]['shareholderEquity'];
                },
                shareholderEquityDisplay(){
                    return numeral(this.shareholderEquity).format('0.00 a');
                },
                netDebtToEquity() {
                    return this.$store.getters.netDebtToEquity();
                },
                returnOnEquity() {
                    return this.$store.getters.returnOnEquity();
                },
                cashFlowRaw(){
                    return parseInt(this.$store.state.financials[0]['cashFlow']);
                },
                cashFlow(){
                    return numeral(this.cashFlowRaw).format('0.00 a');
                },
                netIncome(){
                    let n = this.$store.state.financials[0]['netIncome'];
                    n = numeral(n).format('0.00 a');
                    return n;
                },
                lifeRatio() {
                    return this.$store.getters.lifeRatio();
                },
                PaymentsToAcquirePropertyPlantAndEquipment(){
                    let n = this.$store.getters.capitalExpenditures();
                    n = numeral(n).format('0.00 a');
                    return n;
                },
                vampireRatio() {
                    return this.$store.getters.vampireRatio();
                },
                daysSalesOutstanding() {
                    return this.$store.getters.daysSalesOutstanding();
                },
                daysInventory() {
                    return this.$store.getters.daysInventory();
                },
                daysPayablesPeriod() {
                    return this.$store.getters.daysPayablesPeriod();
                },
                cashConversionCycleDays() {
                    return this.$store.getters.cashConversionCycleDays();
                },
        },
        mounted() {
            this.loadChart();
        },
        methods: {
            loadChart : function () {
                /* eslint-disable no-unused-vars */
                      var ctx = document.getElementById("myChart");
                      var lineChart = new Chart(ctx, {
                        type: 'line',
                        options: {
                          responsive: true,
                            legend: {
                              display: false,
                            },
                          title: {
                                      display: false,
                                      text: 'Operating Income Trend'
                                    },
                                    tooltips: {
                                        mode: 'index',
                                        intersect: false,
                                    },
                                    hover: {
                                        mode: 'nearest',
                                        intersect: true
                                    },
                                    scales: {
                                        xAxes: [{
                                            display: true,
                                            scaleLabel: {
                                                display: true,
                                                labelString: 'Year'
                                            }
                                        }],
                                        yAxes: [{
                                            display: false,
                                            scaleLabel: {
                                                display: true,
                                                labelString: 'Operating Income'
                                            }
                                        }]
                                    }
                        },
                        data: {
                          labels: [ (this.reportedYear - 2), (this.reportedYear - 1) , this.reportedYear],
                          datasets: [{
                             fill: true,
                            data: this.ebitTrend,
                          }]
                        },
                        // source: https://github.com/chartjs/Chart.js/issues/3071#issuecomment-371001496
                        plugins: [{
                          beforeRender: function(c, options) {
                            var dataset = c.data.datasets[0];
                            var yScale = c.scales['y-axis-0'];
                            var yPos = yScale.getPixelForValue(0);

                            var fillPos = limitToZeroOne(yPos / c.height - 0.01);
                            var fillNeg = limitToZeroOne(yPos / c.height + 0.01);

                            var gradientFill = c.ctx.createLinearGradient(0, 0, 0, c.height);
                            gradientFill.addColorStop(0, posColour);
                            gradientFill.addColorStop(fillPos, posColour);
                            gradientFill.addColorStop(fillNeg, negColour);
                            gradientFill.addColorStop(1, negColour);

                            var model = c.data.datasets[0]._meta[Object.keys(dataset._meta)[0]].$filler.el._model;
                            model.backgroundColor = gradientFill;
                          }
                        }]
                      })
                /* eslint-enable no-unused-vars */
            }
        }
    }
</script>

<style>
#quantitative, #qualitative, #valuation {
    border-left: 5px solid;
    background-color:  rgba(27,21,100,.2);
    padding: 8px;
    padding-left: 15px!important;
    margin-top: 10px;
}
.subsection {
    border: 2px solid rgba(27,21,100,.5);
    padding: 5px;
    margin: 5px;
    }
</style>