<template>
  <div>
   <multiselect v-model="selected_stock"
                :options="stocks"
                track-by="Symbol"
                label="Symbol"
                :custom-label="nameWithSymbol"
                :limit=1
                tagPosition="bottom"
                openDirection="bottom"
                @close="updateStock"
                @search-change="itIsLoading"
                placeholder="Please choose the symbol or name of the stock you want to analyse">
   </multiselect>
  </div>
</template>

<script>
  import Multiselect from 'vue-multiselect'
  import 'vue-multiselect/dist/vue-multiselect.min.css';

  export default {
  name: 'GimCalculator',
    components: {
    Multiselect
    },
  props: {
  },
    data: function () {
      return {
        selected_stock: {Name: "Please choose the symbol or name of the stock", Symbol:"you want to analyse"}
      }
    },
      computed: {
      stocks: function() {
        return this.$store.state.stocks;
        }
    },
    methods: {
    updateStock: function(){
        // Update the selected stock
        this.$store.dispatch('updateSelectedStocks', this.selected_stock);
      },
        itIsLoading: function(){
        // Set it to loading first
        this.$store.dispatch('itIsLoading');
        },
    nameWithSymbol: function({ Name, Symbol }) {
      return `${Name} [${Symbol}]`
        }
    },

    mounted: function() {
      this.$store.dispatch('fetchStocks');
    }
  }
</script>

