<template>
   <v-container id="qualitative">
        <v-row>
            <h3>Qualitative Analysis</h3>
        </v-row>

       <v-row>
           <v-col cols="8">
               <v-tooltip bottom>
                   <template v-slot:activator="{ on, attrs }">
                       <h4 v-bind="attrs" v-on="on">
                           Is the market capitalisation below 3 Billion USD?
                       </h4>
                          </template>
                    <span>Find this in morningstar.com or Google Finance.
                   Exceptions are platform companies like Google and Facebook.</span>
               </v-tooltip>
           </v-col>
           <v-col cols="4">
               <v-switch
                       @change="updateQualitative"
                       class="switch"
                       v-model="qualitative.market_cap"
                      :label="`${qualitative.market_cap ? 'Yes' : 'No'}`"
                      color="success"
                      hide-details
            ></v-switch>
           </v-col>
       </v-row>

       <v-row>
           <v-col cols="8">
               <v-tooltip bottom>
                   <template v-slot:activator="{ on, attrs }">
                       <h4 v-bind="attrs" v-on="on">
                           Do you foresee a growth potential?
                       </h4>
                          </template>
                    <span>Is the earnings growing by more than 10% per year?</span>
               </v-tooltip>
           </v-col>
           <v-col cols="4">
               <v-switch
                       @change="updateQualitative"
                       class="switch"
                      v-model="qualitative.growth_potential"
                      :label="`${qualitative.growth_potential ? 'Yes' : 'No'}`"
                      color="success"
                      hide-details
            ></v-switch>
           </v-col>
       </v-row>

       <v-row>
           <v-col cols="8">
               <v-tooltip bottom>
                   <template v-slot:activator="{ on, attrs }">
                       <h4 v-bind="attrs" v-on="on">
                            Is this a quality and differentiated company?
                       </h4>
                          </template>
                    <span>Do they have pricing power?
                        Are there substitutes? Is it scalable?</span>
               </v-tooltip>
           </v-col>
           <v-col cols="4">
               <v-switch
                       @change="updateQualitative"
                       class="switch"
                      v-model="qualitative.quality"
                      :label="`${qualitative.quality ? 'Yes' : 'No'}`"
                      color="success"
                      hide-details
            ></v-switch>
           </v-col>
       </v-row>

       <v-row>
           <v-col cols="8">
               <v-tooltip bottom>
                   <template v-slot:activator="{ on, attrs }">
                       <h4 v-bind="attrs" v-on="on">
                            Do you see longevity in the business?
                       </h4>
                          </template>
                    <span>Is it cyclical?
                        Can it continue to grow more than 5 years to come?</span>
               </v-tooltip>
           </v-col>
           <v-col cols="4">
               <v-switch
                       @change="updateQualitative"
                       class="switch"
                      v-model="qualitative.longevity"
                      :label="`${qualitative.longevity ? 'Yes' : 'No'}`"
                      color="success"
                      hide-details
            ></v-switch>
           </v-col>
       </v-row>

       <v-row>
           <v-col cols="8">
               <v-tooltip bottom>
                   <template v-slot:activator="{ on, attrs }">
                       <h4 v-bind="attrs" v-on="on">
                           Does the management has high ownership ?
                       </h4>
                          </template>
                    <span>Extract management's shareholdings
                        [from gurufocus.com/marketscreener.com]</span>
               </v-tooltip>
           </v-col>
           <v-col cols="4">
               <v-switch
                       @change="updateQualitative"
                       class="switch"
                       v-model="qualitative.ownership"
                      :label="`${qualitative.ownership ? 'Yes' : 'No'}`"
                      color="success"
                      hide-details
            ></v-switch>
           </v-col>
       </v-row>
   </v-container>
</template>

<script>
    export default {
        name: "Qualitative.vue",
        data: function(){
            return {
                qualitative : {
                    market_cap: true,
                  growth_potential: true,
                  quality: true,
                  longevity: true,
                  ownership: true,
                }
            }
        },
        methods: {
            updateQualitative: function(){
        // Update the qualitative factors
        this.$store.dispatch('updateQualitative', this.qualitative);
      },
        }
    }
</script>

<style scoped>
.switch {
    margin-top: 0px!important;
}
</style>