import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex);

// For development
// const k3y = 'pk_1ad65179ef78429f9964a455bcfab493';

// For production
const k3y = 'pk_3faf8c3c8a2449d2bb6b13b00778d904';

const finnk3y = 'bs7k4uvrh5rbhhsbgrgg';

export default new Vuex.Store({
    state: {
        stocks: [],
        selected_stock: {Name: "Please choose the symbol or name of the stock", Symbol:"you want to analyse"},
        is_stock_selected: false,
        is_valuation : false,
        financials: [  // To return 1 period, because it is expensive, to grab the other 2 periods from incomes
            {          // To put dummy data first
                "reportDate": "2020-01-31",
                "fiscalDate": "2020-01-31",
                "currency": "USD",
                "grossProfit": 1,
                "costOfRevenue": 1,
                "operatingRevenue": 1,
                "totalRevenue": 1,
                "operatingIncome": 1,
                "netIncome": 1,
                "operatingExpense": 1,
                "currentCash": 1,
                "totalCash": 1,
                "totalDebt": 1,
                "shareholderEquity": 1,
                "cashChange": 1,
                "cashFlow": 1
            }
        ],
        income: [], // To calculate the past 3 periods income
        cash_flow: {},
        balancesheet: {},
        qualitative : {
                  market_cap: true,
                  growth_potential: true,
                  quality: true,
                  longevity: true,
                  ownership: true,
                },
        share_price: 0,
        advanced_stock: { marketCapitalization: 0},
    },
    mutations: {
        setStocks(state, payload) {
            state.stocks = payload;
        },
        setSelectedStocks(state, payload){
            state.selected_stock = payload;
        },
        setFinancials(state, payload){
            state.financials = payload;
        },
        setIncome(state, payload){
            state.income = payload;
        },
        setCash_flow(state, payload){
            state.cash_flow = payload;
        },
        setBalancesheet(state, payload){
            state.balancesheet = payload;
        },
        setSelectedStockState(state, payload){
            state.is_stock_selected = payload;
        },
        setIs_valuationState(state, payload){
            state.is_valuation = payload;
        },
        setQualitative(state, payload){
            state.qualitative = payload;
        },
        setShare_price(state, payload){
            state.share_price = payload;
        },
        setAdvanced_stock(state, payload){
            state.advanced_stock = payload;
        },
    },
    actions: {
        fetchStocks(context) {
                // Grab data from our locally hosted JSON file
                axios.get('data/stocks.json').then(response => {
                // console.log(response.data);
                context.commit('setStocks', response.data);
            });
        },
        // Set the loading state
        itIsLoading (context){
            context.commit('setSelectedStockState', false);
        },
        updateSelectedStocks(context, stock){
            // Set it as the current analysed stock
            context.commit('setSelectedStocks', stock);
            // Fetch all the data required
            context.dispatch('fetchFinancials');
            context.dispatch('fetchIncome');
            context.dispatch('fetchCash_flow');
            context.dispatch('fetchBalancesheet');
            context.dispatch('fetchAdvanced_stock');
        },
        fetchFinancials(context){
                // Grab data from iex
                axios.get(`https://cloud.iexapis.com/stable/stock/${this.state.selected_stock['Symbol']}/financials?token=${k3y}&period=annual`).then(response => {
                // console.log(response.data);
                context.commit('setFinancials', response.data['financials']);
            });
        },
        fetchIncome(context){
                // Grab data from iex
                axios.get(`https://cloud.iexapis.com/stable/stock/${this.state.selected_stock['Symbol']}/income?token=${k3y}&period=annual&last=3`).then(response => {
                // console.log(response.data);
                context.commit('setIncome', response.data['income']);
                // Render the views after last API executed
                context.commit('setSelectedStockState', true);
            });
        },
        fetchCash_flow(context){
                // Grab data from iex
                axios.get(`https://cloud.iexapis.com/stable/stock/${this.state.selected_stock['Symbol']}/cash-flow?token=${k3y}&period=annual`).then(response => {
                // console.log(response.data);
                context.commit('setCash_flow', response.data['cashflow'][0]);
            });
        },
        fetchBalancesheet(context){
                // Grab data from iex
                axios.get(`https://cloud.iexapis.com/stable/stock/${this.state.selected_stock['Symbol']}/balance-sheet?token=${k3y}`).then(response => {
                // console.log(response.data);
                context.commit('setBalancesheet', response.data['balancesheet'][0]);
            });
        },
        updateQualitative(context, qualitative){
            // Synchronise qualitative
            context.commit('setQualitative', qualitative);
        },
        fetchShare_price(context){
                // Grab data from iex
                axios.get(`https://cloud.iexapis.com/stable/stock/${this.state.selected_stock['Symbol']}/quote/delayedPrice?token=${k3y}`).then(response => {
                // console.log(response.data);
                context.commit('setShare_price', response.data);
                // Only when data is back render the views
                context.commit('setIs_valuationState', true);
            });
        },
        fetchAdvanced_stock(context){
                // Grab data from iex
                axios.get(`https://finnhub.io/api/v1/stock/profile2?symbol=${this.state.selected_stock['Symbol']}&token=${finnk3y}`).then(response => {
                // console.log(response.data);
                context.commit('setAdvanced_stock', response.data);
            });
        },
        fetchDataForValuation(context){
            // Fetch all the data required
            context.dispatch('fetchShare_price');
        },
    },
    getters: {
        operatingIncome(state) {
            return function () {
                return parseInt(state.income[0]['operatingIncome']);
            }
        },
        operatingIncomeLast(state) {
            return function () {
                return parseInt(state.income[1]['operatingIncome']);
            }
        },
        operatingIncomeLastLast(state) {
            return function () {
                return parseInt(state.income[2]['operatingIncome']);
            }
        },
        reportedYear(state) {
            return function () {
                let dateOfReport = state.financials[0]['reportDate'];
                dateOfReport = dateOfReport.slice(0,4);
                return parseInt(dateOfReport);
            }
        },
        totalRevenue(state){
            return function(){
                return state.financials[0]['totalRevenue'];
            }
        },
        operatingProfitMargin(state, getters){
            return function () {
                    return (getters.operatingIncome() / getters.totalRevenue() * 100).toFixed(2);
            }
        },
        cagr(state, getters){
            return function () {
                    return ((Math.pow((getters.operatingIncome() / getters.operatingIncomeLastLast()), 0.5) - 1) * 100).toFixed(2);
            }
        },
        totalDebt(state){
            return function(){
                let debt = state.financials[0]['totalDebt'];
                if (debt === null){
                    debt = 0
                }
                return parseInt(debt);
            }
        },
        netDebtToEquity(state, getters){
            return function () {
                    return (((getters.totalDebt() - state.financials[0]['totalCash']) / state.financials[0]['shareholderEquity']) * 100).toFixed(2);
            }
        },
        returnOnEquity(state){
            return function () {
                    return ((state.financials[0]['netIncome'] / state.financials[0]['shareholderEquity'] * 100).toFixed(2));
            }
        },
        lifeRatio(state){
            return function () {
                    return ((state.financials[0]['cashFlow'] / state.financials[0]['netIncome']).toFixed(2));
            }
        },
        capitalExpenditures(state){
            return function () {
                    return (parseInt(state.cash_flow['capitalExpenditures']) * -1);
            }
        },
        vampireRatio(state, getters){
            return function () {
                    return ((getters.capitalExpenditures() / state.financials[0]['cashFlow']).toFixed(2));
            }
        },
        AccountsReceivableNetCurrent(state){
            return function () {
                return parseFloat(state.balancesheet['receivables']);
            }
        },
        daysSalesOutstanding(state, getters){
            return function () {
                    return parseFloat((getters.AccountsReceivableNetCurrent() / state.financials[0]['totalRevenue'] * 365).toFixed(1));
            }
        },
        // There are companies with no inventories
        InventoryNet(state){
            return function () {
                let InventoryNet = state.balancesheet['inventory'];
                if (InventoryNet === null || InventoryNet === undefined){
                    InventoryNet = 0}
                return parseFloat(InventoryNet);
            }
        },
        daysInventory(state, getters){
            return function () {
                    return parseFloat((getters.InventoryNet() / state.financials[0]['costOfRevenue'] * 365).toFixed(1));
            }
        },
        daysPayablesPeriod(state){
            return function () {
                    return parseFloat((state.balancesheet['accountsPayable'] / state.financials[0]['costOfRevenue'] * 365).toFixed(1));
            }
        },
        cashConversionCycleDays(state, getters){
            return function () {
                    return (getters.daysSalesOutstanding() + getters.daysInventory() - getters.daysPayablesPeriod()).toFixed(1);
            }
        },
        enterpriseValue(state){
            return function () {
                    return parseInt(state.advanced_stock['marketCapitalization'] * 1000000);
            }
        },
        totalScore(state, getters){
            return function () {
                let totalScore = 0.0;

                if (getters.operatingProfitMargin() >= 8){
                    totalScore += 0.5;
                }

                if (getters.cagr() >= 10){
                    totalScore += 1.0;
                }

                if (getters.netDebtToEquity() <= 40){
                    totalScore += 1.5;
                }

                if (getters.returnOnEquity() >= 15){
                    totalScore += 1.5;
                }
                if (getters.lifeRatio() >= 0.8){
                    totalScore += 1.0;
                }
                if (getters.vampireRatio() <= 0.5){
                    totalScore += 1.0;
                }
                if (getters.cashConversionCycleDays() <= 130){
                    totalScore += 1.0;
                }
                if (state.qualitative['market_cap']){
                    totalScore += 0.5;
                }
                if (state.qualitative['growth_potential']){
                    totalScore += 0.5;
                }
                if (state.qualitative['quality']){
                    totalScore += 0.5;
                }
                if (state.qualitative['longevity']){
                    totalScore += 0.5;
                }
                if (state.qualitative['ownership']){
                    totalScore += 0.5;
                }
                return totalScore;
            }
        },
    }
})