<template>
    <v-container id="valuation">
        <v-row align="center" justify="center">
            <h3>Total Score : {{totalScore}}</h3>
        </v-row>
        <v-row align="center" justify="center">
            <p>IF SCORE > 7.5, CLICK BELOW TO PROCEED TO VALUATION</p>
            <v-btn block color="green" @click="letsValuate">VALUATION</v-btn>
        </v-row>

        <v-row v-if="valuation_state">
            <v-container id="valuation_inner">
                <v-row no-gutters id="intrinsic_value">
                    <v-col cols="10">
                        <h3>Intrinsic Value</h3>
                    </v-col>
                    <v-col cols="2">
                        <u><b>{{intrinsicValue}}</b></u>
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-alert width="100%" color="red" elevation="12" v-if="share_price > intrinsicValue">
                        OVERVALUED
                    </v-alert>
                    <v-alert width="100%" color="green" elevation="12" v-else-if="intrinsicValue > share_price">
                        UNDERVALUED
                    </v-alert>
                     <v-alert width="100%" color="yellow" elevation="12" v-else>
                        FAIR VALUED
                    </v-alert>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>Current Share Price</h3>
                    </v-col>
                    <v-col cols="2">
                        <u>{{share_price}}</u>
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>Enterprise Value</h3>
                    </v-col>
                    <v-col cols="2">
                        {{ enterpriseValueDisplay }}
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>Current Operating Profit</h3>
                    </v-col>
                    <v-col cols="2">
                        {{ operatingIncomeDisplay }}
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>Current EV/EBIT</h3>
                    </v-col>
                    <v-col cols="2">
                        {{currentEVoverEBIT}}
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>Expected Growth %</h3>
                    </v-col>
                    <v-col cols="2">
                        <v-text-field
                                v-model="expected_growth"
                                outlined
                                solo
                                single-line
                                dense
                        ></v-text-field>
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>Forward Operating Profit</h3>
                    </v-col>
                    <v-col cols="2">
                        {{  forwardOperatingProfitDisplay  }}
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>Forward EV/EBIT</h3>
                    </v-col>
                    <v-col cols="2">
                        {{forwardEVoverEBIT}}
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>How much EV/EBIT do you want to pay?</h3>
                    </v-col>
                    <v-col cols="2">
                        <v-text-field
                                v-model="evEbitToPay"
                                outlined
                                solo
                                single-line
                                dense
                        >
                        </v-text-field>
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>Upside / Downside</h3>
                    </v-col>
                    <v-col cols="2">
                        {{upSideDownSide}}
                    </v-col>
                </v-row>
                <v-row no-gutters>
                    <v-col cols="10">
                        <h3>Upside / Downside (%)</h3>
                    </v-col>
                    <v-col cols="2">
                        {{upSideDownSidePercent}} %
                    </v-col>
                </v-row>
            </v-container>
        </v-row>
    </v-container>
</template>

<script>
    var numeral = require('numeral');

    export default {
        name: "Valuation.vue",
        computed: {
            valuation_state(){
                return this.$store.state.is_valuation;
            },
            totalScore() {
                    return this.$store.getters.totalScore();
                },
            share_price() {
                    return parseFloat(this.$store.state.share_price);
                },
            enterpriseValue() {
                    return this.$store.getters.enterpriseValue();
                },
            enterpriseValueDisplay() {
                    return numeral(this.enterpriseValue).format('0.00 a');
            },
            operatingIncome() {
                    return parseInt(this.$store.getters.operatingIncome());
                },
            operatingIncomeDisplay() {
                    return numeral(this.operatingIncome).format('0.00 a');
                },
            currentEVoverEBIT() {
              return (this.enterpriseValue / this.operatingIncome).toFixed(2);
            },
            forwardOperatingProfit(){
                return parseInt(this.operatingIncome * (1 + this.expected_growth / 100));
            },
            forwardOperatingProfitDisplay(){
                return numeral(this.forwardOperatingProfit).format('0.00 a');
            },
            forwardEVoverEBIT(){
                return (this.enterpriseValue / this.forwardOperatingProfit).toFixed(2);
            },
            upSideDownSide(){
                return parseFloat(((this.evEbitToPay/this.forwardEVoverEBIT - 1) * this.share_price).toFixed(2));
            },
            upSideDownSidePercent(){
                return (this.upSideDownSide / this.share_price * 100).toFixed(2);
            },
            intrinsicValue(){
                return (this.share_price + this.upSideDownSide).toFixed(2);
            },
            cagr() {
                return this.$store.getters.cagr();
            },
        },
        mounted: function(){
            this.expected_growth = this.cagr;
        },
       data: function(){
            return {
                expected_growth: 10, // must be in decimals but display in percentage
                evEbitToPay : 15,
                }
        },
        methods: {
        letsValuate: function(){
             // Fetch the data needed
            this.$store.dispatch('fetchDataForValuation');
          },
        }
    }
</script>

<style scoped>
#valuation_inner, #intrinsic_value {
    border: 2px solid;
    background-color:  rgba(27,21,100,.2);
    padding: 20px;
    margin-top: 10px;
}
</style>